import React from "react";
import "./Modulos.css";

function Modulos() {
  const modulos = [
    {
      id: 1,
      nome: "Mdulo de Segurana",
      verso: "3.2.1",
      status: "Ativo",
      conexes: 8,
    },
    {
      id: 2,
      nome: "Mdulo de Comunicao",
      verso: "2.8.5",
      status: "Ativo",
      conexes: 15,
    },
    {
      id: 3,
      nome: "Mdulo de Processamento",
      verso: "4.1.0",
      status: "Ativo",
      conexes: 6,
    },
    {
      id: 4,
      nome: "Mdulo de Armazenamento",
      verso: "2.4.7",
      status: "Desativado",
      conexes: 0,
    },
    {
      id: 5,
      nome: "Mdulo de Anlise",
      verso: "1.9.3",
      status: "Ativo",
      conexes: 4,
    },
    {
      id: 6,
      nome: "Mdulo de Interface",
      verso: "3.6.2",
      status: "Ativo",
      conexes: 12,
    },
  ];

  const atualizacoes = [
    {
      id: 1,
      modulo: "Mdulo de Segurana",
      verso_atual: "3.2.1",
      nova_verso: "3.3.0",
      prioridade: "Alta",
    },
    {
      id: 2,
      modulo: "Mdulo de Comunicao",
      verso_atual: "2.8.5",
      nova_verso: "3.0.0",
      prioridade: "Mdia",
    },
    {
      id: 3,
      modulo: "Mdulo de Interface",
      verso_atual: "3.6.2",
      nova_verso: "3.6.5",
      prioridade: "Baixa",
    },
  ];

  return (
    <div className="modulos">
      <div className="dashboard-header">
        <h2>GERENCIAMENTO DE MDULOS</h2>
        <div className="module-actions">
          <button className="primary-btn">ADICIONAR MDULO</button>
        </div>
      </div>

      <div className="dashboard-row modules-overview">
        <div className="summary-box">
          <div className="summary-value">6</div>
          <div className="summary-label">TOTAL DE MDULOS</div>
        </div>
        <div className="summary-box">
          <div className="summary-value">5</div>
          <div className="summary-label">MDULOS ATIVOS</div>
        </div>
        <div className="summary-box">
          <div className="summary-value">1</div>
          <div className="summary-label">MDULOS DESATIVADOS</div>
        </div>
        <div className="summary-box">
          <div className="summary-value">3</div>
          <div className="summary-label">ATUALIZAES DISPONVEIS</div>
        </div>
      </div>

      <div className="modulos-grid">
        <div className="modulos-card">
          <div className="card-header">
            <h3>MDULOS INSTALADOS</h3>
          </div>
          <table className="data-table">
            <thead>
              <tr>
                <th>ID</th>
                <th>MDULO</th>
                <th>VERSO</th>
                <th>STATUS</th>
                <th>CONEXES</th>
                <th>AES</th>
              </tr>
            </thead>
            <tbody>
              {modulos.map((modulo) => (
                <tr key={modulo.id}>
                  <td>#{modulo.id.toString().padStart(3, "0")}</td>
                  <td>{modulo.nome}</td>
                  <td>{modulo.verso}</td>
                  <td>
                    <span
                      className={`status-${
                        modulo.status === "Ativo" ? "active" : "inactive"
                      }`}
                    >
                      {modulo.status}
                    </span>
                  </td>
                  <td>{modulo.conexes}</td>
                  <td>
                    <div className="action-buttons">
                      {modulo.status === "Ativo" ? (
                        <button className="action-btn deactivate">
                          Desativar
                        </button>
                      ) : (
                        <button className="action-btn activate">Ativar</button>
                      )}
                      <button className="action-btn config">Configurar</button>
                    </div>
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
      </div>

      <div className="dashboard-row">
        <div className="dashboard-card updates-card">
          <div className="card-header">
            <h3>ATUALIZAES DISPONVEIS</h3>
          </div>
          <table className="data-table">
            <thead>
              <tr>
                <th>MDULO</th>
                <th>VERSO ATUAL</th>
                <th>NOVA VERSO</th>
                <th>PRIORIDADE</th>
                <th>AES</th>
              </tr>
            </thead>
            <tbody>
              {atualizacoes.map((atualizacao) => (
                <tr key={atualizacao.id}>
                  <td>{atualizacao.modulo}</td>
                  <td>{atualizacao.verso_atual}</td>
                  <td>{atualizacao.nova_verso}</td>
                  <td>
                    <span
                      className={`priority-${atualizacao.prioridade.toLowerCase()}`}
                    >
                      {atualizacao.prioridade}
                    </span>
                  </td>
                  <td>
                    <button className="action-btn update">Atualizar</button>
                    <button className="action-btn details">Detalhes</button>
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>

        <div className="dashboard-card dependencies-card">
          <div className="card-header">
            <h3>DIAGRAMA DE DEPENDNCIAS</h3>
          </div>
          <div className="dependencies-diagram">
            <div className="diagram-placeholder">
              <span className="diagram-icon"></span>
              <span className="diagram-text">
                VISUALIZAO DE DEPENDNCIAS ENTRE MDULOS
              </span>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Modulos;
